.search-form {
  position: relative;
}

.search-modal__hide {
  display: none;
}

.search-modal__show {
  display: block;
}

.search-modal {
  z-index: 1000;
  width: 100%;
  position: absolute;
  top: 30px;
  left: 0;
}

.search-modal__wrapper {
  max-width: 300px;
  max-height: calc(100vh - 200px);
  background-color: #fff;
  overflow: auto;
}

.search-modal__title, .search-modal-film__error {
  text-align: center;
  color: #0c111b80;
  margin: 10px 0 0;
  font-size: 14px;
}

.search-modal__content {
  padding: 12px;
}

.search-modal-film {
  min-height: 70px;
  gap: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
  padding-right: 10px;
  display: flex;
}

.search-modal-film__img {
  width: 70px;
  height: 70px;
  display: block;
}

.search-modal-film__descr {
  flex-direction: column;
  display: flex;
}

.search-modal-film__title {
  color: #0c111b;
  font-size: 12px;
  font-weight: 700;
}

.fade {
  animation-name: fade;
  animation-duration: .4s;
}

@keyframes fade {
  from {
    opacity: .1;
  }

  to {
    opacity: 1;
  }
}

html {
  box-sizing: border-box;
}

body {
  background-color: #0c111b;
  margin: 0;
  font-family: Roboto, sans-serif;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}

